// app/display/[id]/page.js
import { prisma } from "@/lib/prisma";
import { notFound } from "next/navigation";

export default async function RecipeDisplay({ params }) {
  // 1. Get the params
  const { id } = await params;
  
  // 2. Convert to number
  const numericId = parseInt(id, 10);

  // DEBUG: This will show up in your terminal (not the browser)
  console.log("--- DEBUG START ---");
  console.log("Raw ID from URL:", id);
  console.log("Converted Numeric ID:", numericId);

  // 3. Try the Database
  let recipe;
  try {
    recipe = await prisma.recipes.findUnique({
      where: { id: numericId }
    });
    console.log("Database result:", recipe ? "Found!" : "Not Found");
  } catch (error) {
    console.error("Prisma Error:", error.message);
    return <div className="p-10 border-red-500 border-2">Database Connection Error: {error.message}</div>;
  }

  if (!recipe) {
    // If the DB returns nothing, check if the ID actually exists in Neon
    return (
      <div className="p-10">
        <h1 className="text-red-500">Recipe Not Found</h1>
        <p>We looked for ID: <strong>{numericId}</strong> in the <strong>recipe_management</strong> schema.</p>
      </div>
    );
  }

  // 4. Parse your data
  const ingredients = JSON.parse(recipe.ingredients);

  return (
    <div className="p-10">
      <h1 className="text-4xl font-bold">{recipe.title}</h1>
      <p className="mt-4">ID: {recipe.id}</p>
      <div className="mt-6">
        <h3 className="font-bold">Ingredients:</h3>
        {ingredients.map((ing, i) => (
          <p key={i}>â€¢ {ing.ingredient}</p>
        ))}
      </div>
    </div>
  );
}